import { Component, OnInit } from '@angular/core';
import { VhAlgorithm } from 'ionic-vhframeworks';
import { NzCascaderOption } from 'ng-zorro-antd/cascader';

@Component({
  selector: 'app-bill',
  templateUrl: './bill.component.html',
  styleUrls: ['./bill.component.scss']
})
export class BillComponent implements OnInit {

  constructor(
    public vhAlgorithm: VhAlgorithm
  ) { }

  ngOnInit(): void {
    this.intStr = parseInt(this.editStr);
  }
  intStr: any;
  discount: any;
  tax: any;
  payment: any;
  ngAfterViewInit() {
    this.vhAlgorithm.waitingStack().then(() => {
      this.discount = this.vhAlgorithm.vhnumeral(".discount");
      this.tax = this.vhAlgorithm.vhnumeral(".tax");
      this.payment = this.vhAlgorithm.vhnumeral(".payment-price");
    });
  }
  type_payment: any = '1';
  id_tax: any = 'a';
  options = [
    {
      value: '1',
      label: 'Tin mt',
      isLeaf: true
    },
    {
      value: '2',
      label: 'Cng n',
      isLeaf: true
    },
    {
      value: '3',
      label: 'Ti khon',
      children: [
        {
          value: 'vietcombank',
          label: 'Vietcombank',
          isLeaf: true
        },
        {
          value: 'sacombank',
          label: 'Sacombank',
          isLeaf: true
        }
      ]
    }]

  nzOptions: NzCascaderOption[] = this.options;
  value1: string[] | null = null;

  onChanges(values: string[]): void {
  }

  value: any = 0;
  valuetax: any = 0;
  editStr: any = '0';

}
