import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { VhAlgorithm, VhQuerySales } from 'ionic-vhframeworks';

@Component({
  selector: 'app-customer',
  templateUrl: './customer.component.html',
  styleUrls: ['./customer.component.scss']
})
export class CustomerComponent implements OnInit {
  @Input("id_customer") id_customer;
  @Output() closeAndReceiveCustomer = new EventEmitter<any>();
  debt: boolean = null;
  public customerList: any[] = []

  public searchList: any[] = [];
  constructor(
    public vhAlgorithm: VhAlgorithm,
    private vhQuerySales: VhQuerySales
  ) {
    this.searchList = [...this.customerList]
  }
  ngOnInit() {
    this.getData()
  }
  ngOnDestroy() {
    this.observableCustomers.unsubscribe()
  }
  observableCustomers: any;
  getData() {
    this.observableCustomers = this.vhQuerySales.getObservableLocalCustomers().subscribe((localCustomers: any) => {
      this.customerList = localCustomers.filter(customer => {
        if (customer['_id'] != "id_retail") {
          if (this.debt == null) return true
          else if (customer['debt_enable'] == this.debt) return true
          else return false
        } return false
      }).map( item => {return {...item, earning_point : item.earning_point ? item.earning_point : 0}})
      this.id_customer = this.id_customer ? this.id_customer : this.customerList[0]._id;
      this.searchList = this.customerList
    })

  }
  cancel(value) {
    this.closeAndReceiveCustomer.emit(value)
  }

  search(value: string) {
    let tempVal: string = this.vhAlgorithm.changeAlias(value.toLowerCase());
    if (value.length)
      this.searchList = this.vhAlgorithm.searchList(tempVal, this.customerList, ['name','phone'])
    else this.searchList = this.customerList
  }
}
