import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-retail-info',
  templateUrl: './retail-info.component.html',
  styleUrls: ['./retail-info.component.scss']
})
export class RetailInfoComponent implements OnInit {
  @Input("showDrawerAddRetail") showDrawerAddRetail: boolean = false;
  @Input("retail_name") retail_name: string = '';
  @Input("retail_phone") retail_phone: string = '';
  @Input("retail_address") retail_address: string = '';
  @Output("closeAddCustomer") closeAddCustomer = new EventEmitter<any>();

  constructor() { }
  ngOnInit(): void {
    this.initaddRetailForm()
  }
  addRetailForm: FormGroup;
  close(success?) {
    this.closeAddCustomer.emit(success ? this.addRetailForm.value : false);
  }
  initaddRetailForm() {
    this.addRetailForm = new FormGroup({
      retail_name: new FormControl(this.retail_name, Validators.compose([Validators.required])),
      retail_phone: new FormControl(this.retail_phone, Validators.compose([Validators.required, Validators.minLength(6)])),
      retail_address: new FormControl(this.retail_address),
    });
  }
}
