import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { VhAlgorithm, VhQuerySales } from 'ionic-vhframeworks';

@Component({
  selector: 'app-search-customer',
  templateUrl: './search-customer.component.html',
  styleUrls: ['./search-customer.component.scss']
})
export class SearchCustomerComponent implements OnInit {
  @Input("id_customer") id_customer;
  @Output() closeAndReceiveCustomer = new EventEmitter<any>();
  debt: boolean = null;
  public customerList: any[] = []

  public searchList: any[] = [];
  constructor(
    public vhAlgorithm: VhAlgorithm,
    private vhQuerySales: VhQuerySales
  ) {
    this.searchList = [...this.customerList]
  }
  ngOnInit() {

  }
  cancel(value) {
    this.closeAndReceiveCustomer.emit(value)
  }

  timer: any;
  search(value: string) {
    clearTimeout(this.timer);
    // sau 500ms th thc hin
    this.timer = setTimeout(() => {
      let tempVal: string = this.vhAlgorithm.changeAlias(value.toLowerCase());
      if (value.length >= 6) {
        this.vhQuerySales.searchKeyword_NamePhone('customers', tempVal).then((res: any) => {
          this.searchList = res.map(item => {
            return { ...item, earning_point: item.earning_point ? item.earning_point : 0 }
          })
        }
        )
      }
    }, 500);

  }
}
